*{
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}

h1{
    color: white;
    text-align: center;
    text-decoration:underline;
}

body{
    height: 10;
    background-color:#2c2c2c;
}
.todoContainer {
    width: 500px;
    margin: 100px auto;

    display: flex;
    flex-direction: column;
}

.todoCreateForm{
    margin: 20px 0;
    display: flex;
    gap: 5px;
}

.todoInput{
    flex-grow: 1;
    width: 100%;
    padding: 10px;
    border-radius: 3px;
    border: solid 1px #999
}

.buttonCreate{
    border: none;
    border-radius: 3px;
    background-color: rgb(26,76,169);
    color: white;
    padding: 10px 20px
}

.todosContainer{
    display: flex;
    flex-direction: column;
    gap: 5px;
    margin-top: 10px;
}

.todo{
    padding: 10px;
    background-color: #2c2c2c;
    color:white;
    border: solid 1px #969696;
    border-radius: 5px;
}

.todoTitle{
    flex: 1;
    font-size: 20px;
}

.todoUpdateForm, .todoInfo{
    display: flex;
    align-items: center;
    gap: 5px;

}

.button{
    border: none;
    border-radius: 3px;
    background-color: rgb(72, 122, 221);
    color: white;
    padding: 10px 20px;

}

.buttonDelete{
    border: none;
    border-radius: 3px;
    background-color: rgb(169, 26, 26);
    color: white;
    padding: 10px 20px;
}